import React, { useState, useEffect, useCallback } from 'react';
import { apiKey } from '../api';
import { Modal, Button, Form, Alert, Pagination } from 'react-bootstrap';

const ItemsTable = ({ ownerReferenceId }) => {
    // State qun l danh sch items
    const [items, setItems] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    // State qun l vic lit k v hy bn
    const [selectedItem, setSelectedItem] = useState(null);
    const [listingPrice, setListingPrice] = useState('');
    const [showListingModal, setShowListingModal] = useState(false);
    const [listingError, setListingError] = useState(null);
    const [isProcessing, setIsProcessing] = useState(false);

    // State mi cho chc nng chnh sa
    const [showEditModal, setShowEditModal] = useState(false);
    const [editItem, setEditItem] = useState(null);
    const [editImageUrl, setEditImageUrl] = useState('');
    const [editAttributes, setEditAttributes] = useState([]);
    const [editError, setEditError] = useState(null);
    const [isEditing, setIsEditing] = useState(false);
    // Thm vo cc state khc ca component
    const [editImageFile, setEditImageFile] = useState(null);
    const [editImagePreview, setEditImagePreview] = useState(null);
    // Cc state mi cho phn trang
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(10);

    //Thm state  qun l loi tin t:
    const [selectedCurrency, setSelectedCurrency] = useState('USDC');
    const availableCurrencies = items
        .filter(item => item.type === 'Currency')
        .map(item => item.item);

    // Thm  u file, ngay sau cc import
    const CLOUDINARY_UPLOAD_PRESET = 'ARTSOLANA';
    const CLOUDINARY_CLOUD_NAME = 'dy3nmkszo';

    // Hm upload nh ln Cloudinary
    const uploadImageToCloudinary = async (file) => {
        const formData = new FormData();
        formData.append('file', file);
        formData.append('upload_preset', CLOUDINARY_UPLOAD_PRESET);
        formData.append('api_key', process.env.REACT_APP_CLOUDINARY_API_KEY);

        try {
            const response = await fetch(
                `https://api.cloudinary.com/v1_1/${CLOUDINARY_CLOUD_NAME}/image/upload`,
                {
                    method: 'POST',
                    body: formData
                }
            );

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.error?.message || 'Upload failed');
            }

            const data = await response.json();
            return data.secure_url;
        } catch (err) {
            console.error('Error uploading to Cloudinary:', err);
            throw new Error('Khng th ti ln hnh nh. Vui lng th li. Chi tit: ' + err.message);
        }
    };

    const handleEditImageChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            if (file.size > 5 * 1024 * 1024) {
                setEditError("Kch thc file khng c vt qu 5MB");
                return;
            }

            if (!file.type.startsWith('image/')) {
                setEditError("Vui lng chn file hnh nh");
                return;
            }

            setEditImageFile(file);

            const reader = new FileReader();
            reader.onloadend = () => {
                setEditImagePreview(reader.result);
            };
            reader.readAsDataURL(file);
        }
    };

    // State qun l b lc th trng
    const [marketFilter, setMarketFilter] = useState('all');

    // Hm tm np danh sch items
    const fetchItems = useCallback(async () => {
        setLoading(true);
        setError(null);

        try {
            // Xy dng URL vi cc tham s lc
            let url = `https://api.gameshift.dev/nx/items`;
            const params = new URLSearchParams();

            // Thm tham s ch s hu nu c
            if (ownerReferenceId) {
                params.append('ownerReferenceId', ownerReferenceId);
            }

            // p dng b lc trng thi bn
            switch (marketFilter) {
                case 'forSale':
                    params.append('forSale', 'true');
                    break;
                case 'notForSale':
                    params.append('priceCents', 'null'); // Kim tra gi tr priceCents l null
                    break;
                default:
                    // Trng hp tt c
                    break;
            }

            // Xy dng URL vi cc tham s tm np
            url += `?${params.toString()}`;

            // Gi API  ly danh sch items
            const response = await fetch(url, {
                headers: {
                    'accept': 'application/json',
                    'x-api-key': apiKey
                }
            });

            // Kim tra phn hi ca API
            if (!response.ok) {
                throw new Error('Khng th ti danh sch items');
            }

            // Chuyn i d liu nhn c thnh JSON
            const data = await response.json();

            // Lc li danh sch nu cn thit
            if (marketFilter === 'notForSale') {
                setItems(data.data.filter(item => item.item.priceCents === null));
            } else {
                setItems(data.data || []);
            }

        } catch (err) {
            setError('Li khi ti items: ' + err.message);
        } finally {
            setLoading(false);
        }
    }, [ownerReferenceId, marketFilter]);




    // Tnh ton cc gi tr phn trang
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = items.slice(indexOfFirstItem, indexOfLastItem);

    // Tnh tng s trang
    const totalPages = Math.ceil(items.length / itemsPerPage);

    // // Hm thay i trang
    // const paginate = (pageNumber) => setCurrentPage(pageNumber);

    // // Hm sinh cc nt phn trang ng
    // const renderPaginationItems = () => {
    //     const paginationItems = [];

    //     // Nt trang u
    //     paginationItems.push(
    //         <Pagination.First
    //             key="first"
    //             onClick={() => setCurrentPage(1)}
    //             disabled={currentPage === 1}
    //         />
    //     );

    //     // Nt trang trc
    //     paginationItems.push(
    //         <Pagination.Prev
    //             key="prev"
    //             onClick={() => setCurrentPage(currentPage - 1)}
    //             disabled={currentPage === 1}
    //         />
    //     );

    //     // Cc nt s trang
    //     const maxPagesToShow = 5;
    //     let startPage = Math.max(1, currentPage - Math.floor(maxPagesToShow / 2));
    //     let endPage = Math.min(totalPages, startPage + maxPagesToShow - 1);

    //     // iu chnh li nu n cui hoc u
    //     if (endPage - startPage + 1 < maxPagesToShow) {
    //         startPage = Math.max(1, endPage - maxPagesToShow + 1);
    //     }

    //     for (let number = startPage; number <= endPage; number++) {
    //         paginationItems.push(
    //             <Pagination.Item
    //                 key={number}
    //                 active={number === currentPage}
    //                 onClick={() => paginate(number)}
    //             >
    //                 {number}
    //             </Pagination.Item>
    //         );
    //     }

    //     // Nt trang k tip
    //     paginationItems.push(
    //         <Pagination.Next
    //             key="next"
    //             onClick={() => setCurrentPage(currentPage + 1)}
    //             disabled={currentPage === totalPages}
    //         />
    //     );

    //     // Nt trang cui
    //     paginationItems.push(
    //         <Pagination.Last
    //             key="last"
    //             onClick={() => setCurrentPage(totalPages)}
    //             disabled={currentPage === totalPages}
    //         />
    //     );

    //     return paginationItems;
    // };

    // // Thm dropdown chn s lng item trn mi trang
    // const renderItemsPerPageSelector = () => {
    //     const pageSizeOptions = [5, 10, 20, 50];
    //     return (
    //         <div className="d-flex align-items-center ms-3">
    //             <span className="me-2">Hin th:</span>
    //             <select
    //                 className="form-select form-select-sm"
    //                 style={{ width: 'auto' }}
    //                 value={itemsPerPage}
    //                 onChange={(e) => {
    //                     setItemsPerPage(Number(e.target.value));
    //                     setCurrentPage(1); // Reset v trang u
    //                 }}
    //             >
    //                 {pageSizeOptions.map(size => (
    //                     <option key={size} value={size}>
    //                         {size} mc
    //                     </option>
    //                 ))}
    //             </select>
    //         </div>
    //     );
    // };

    // T ng ti items khi component mount hoc cc dependency thay i
    useEffect(() => {
        fetchItems();
    }, [ownerReferenceId, marketFilter, fetchItems]);  // Thm fetchItems vo dependencies

    // Hm x l lit k ti sn bn
    const handleListForSale = async () => {
        if (!selectedItem || !listingPrice) {
            setListingError('Vui lng nhp gi hp l');
            return;
        }

        setIsProcessing(true);
        setListingError(null);

        try {
            const response = await fetch(
                `https://api.gameshift.dev/nx/unique-assets/${selectedItem.id}/list-for-sale`,
                {
                    method: 'POST',
                    headers: {
                        'accept': 'application/json',
                        'content-type': 'application/json',
                        'x-api-key': apiKey
                    },
                    body: JSON.stringify({
                        price: {
                            currencyId: selectedCurrency, // S dng selectedCurrency thay v hardcode
                            naturalAmount: listingPrice
                        }
                    })
                }
            );

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || 'Khng th lit k ti sn');
            }

            const data = await response.json();

            // Thay v window.location.href, m tab mi
            if (data.consentUrl) {
                window.open(data.consentUrl, '_blank', 'noopener,noreferrer');
            }

            // Lm mi danh sch sau khi lit k
            fetchItems();
        } catch (err) {
            setListingError(err.message);
        } finally {
            setIsProcessing(false);
            setShowListingModal(false);
        }
    };
    // Hm x l hy bn ti sn
    const handleCancelSale = async (itemId) => {
        setIsProcessing(true);
        setListingError(null);

        try {
            // Gi API  hy bn ti sn vi URL mi cho unique assets
            const response = await fetch(
                `https://api.gameshift.dev/nx/unique-assets/${itemId}/cancel-listing`,
                {
                    method: 'POST',
                    headers: {
                        'accept': 'application/json',
                        'x-api-key': apiKey
                    }
                }
            );

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || 'Khng th hy bn ti sn');
            }

            const data = await response.json();

            // M URL ng  trong tab mi thay v chuyn hng
            if (data.consentUrl) {
                window.open(data.consentUrl, '_blank', 'noopener,noreferrer');
            }

            // Lm mi danh sch sau khi hy bn
            fetchItems();
        } catch (err) {
            setListingError(err.message);
        } finally {
            setIsProcessing(false);
        }
    };


    // M modal  lit k ti sn
    const openListingModal = (item) => {
        setSelectedItem(item);
        setListingPrice('');
        setSelectedCurrency(availableCurrencies[0]?.id || 'USDC');
        setListingError(null);
        setShowListingModal(true);
    };

    // Hm rt gn vn bn
    const truncateText = (text, maxLength) => {
        if (!text) return '-';
        return text.length > maxLength ? text.substring(0, maxLength) + '...' : text;
    };

    // Hm m modal chnh sa
    const openEditModal = (item) => {
        setEditItem(item);
        setEditImageUrl(item.imageUrl || '');
        setEditImageFile(null);
        setEditImagePreview(null);

        // Khi to attributes t item hin ti
        setEditAttributes(item.attributes || []);

        setEditError(null);
        setShowEditModal(true);
    };

    // Hm thm thuc tnh mi
    const addAttribute = () => {
        setEditAttributes([...editAttributes, { traitType: '', value: '' }]);
    };

    // Hm xa thuc tnh
    const removeAttribute = (indexToRemove) => {
        setEditAttributes(editAttributes.filter((_, index) => index !== indexToRemove));
    };

    // Hm cp nht thuc tnh
    const updateAttribute = (index, field, value) => {
        const newAttributes = [...editAttributes];
        newAttributes[index][field] = value;
        setEditAttributes(newAttributes);
    };

    const handleEditAsset = async () => {
        if (!editItem) return;

        setIsEditing(true);
        setEditError(null);

        try {
            let newImageUrl = editImageUrl;

            // Nu c file nh mi, tin hnh upload
            if (editImageFile) {
                newImageUrl = await uploadImageToCloudinary(editImageFile);
            }

            // Chun b payload cho API
            const payload = {
                imageUrl: newImageUrl,
                attributes: editAttributes
            };

            const response = await fetch(
                `https://api.gameshift.dev/nx/unique-assets/${editItem.id}`,
                {
                    method: 'PUT',
                    headers: {
                        'accept': 'application/json',
                        'content-type': 'application/json',
                        'x-api-key': apiKey
                    },
                    body: JSON.stringify(payload)
                }
            );

            if (!response.ok) {
                const errorData = await response.json();
                throw new Error(errorData.message || 'Khng th chnh sa ti sn');
            }

            // Lm mi danh sch sau khi chnh sa
            await fetchItems();

            // ng modal v reset state
            setShowEditModal(false);
            setEditImageFile(null);
            setEditImagePreview(null);
        } catch (err) {
            setEditError(err.message);
        } finally {
            setIsEditing(false);
        }
    };

    // Reset state khi m modal chnh sa



    return (
        <div className="card w-100">
            {/* Tiu  v cc nt iu khin */}
            <div className="card-header ">
                <div className="d-flex justify-content-between align-items-center">
                    <h5 className="card-title mb-0">Danh Sch Items</h5>
                    <div className="d-flex align-items-center">
                        {/* Dropdown lc th trng */}
                        <select
                            className="form-select form-select-sm me-2"
                            style={{ width: 'auto' }}
                            value={marketFilter}
                            onChange={(e) => setMarketFilter(e.target.value)}
                        >
                            <option value="all">Tt C Mt Hng</option>
                            <option value="forSale">ang c Bn</option>
                            <option value="notForSale">Cha c Bn</option>
                        </select>
                        <button
                            onClick={fetchItems}
                            className="btn btn-primary btn-sm"
                        >
                            <i className="fas fa-sync-alt me-1"></i> Lm mi
                        </button>
                    </div>
                </div>
            </div>

            {/* Ni dung danh sch items */}
            <div className="card-body p-0">
                {/* Trng thi ti v li */}
                {loading ? (
                    <div className="d-flex justify-content-center align-items-center p-5">
                        <div className="spinner-border text-primary" role="status">
                            <span className="visually-hidden">Loading...</span>
                        </div>
                    </div>
                ) : error ? (
                    <div className="alert alert-danger m-3">{error}</div>
                ) : items.length === 0 ? (
                    <div className="text-center py-5 text-muted">Khng tm thy items</div>
                ) : (
                    <>
                        <div className="table-responsive">
                            <style>
                                {`
                    @media (max-width: 768px) {
                        .table-responsive {
                            display: block;
                            width: 100%;
                            overflow-x: auto;
                            -webkit-overflow-scrolling: touch;
                        }
                        
                        .mobile-table-wrapper {
                            min-width: 800px;
                        }
                    }

                    .item-image {
                        width: 40px;
                        height: 40px;
                        object-fit: cover;
                        border-radius: 4px;
                        transition: transform 0.2s;
                    }

                    .item-image:hover {
                        transform: scale(3);
                        z-index: 1000;
                    }

                    .table > :not(caption) > * > * {
                        padding: 0.75rem;
                        vertical-align: middle;
                    }

                    .badge-currency {
                        background-color: #e3f2fd;
                        color: #1976d2;
                    }

                    .badge-asset {
                        background-color: #f3e5f5;
                        color: #7b1fa2;
                    }

                    .badge-for-sale {
                        background-color: #e8f5e9;
                        color: #2e7d32;
                    }

                    .table-hover tbody tr:hover {
                        background-color: rgba(0, 0, 0, 0.02);
                    }
                    `}
                            </style>
                            <div className="mobile-table-wrapper">
                                <table className="table table-hover mb-0">
                                    {/* Tiu  bng */}
                                    <thead>
                                        <tr className="bg-light">
                                            <th style={{ width: '90px' }}>Loi</th>
                                            <th style={{ width: '80px' }}>nh</th>
                                            <th style={{ width: '180px' }}>Tn</th>
                                            <th style={{ width: '200px' }}>M t</th>
                                            <th style={{ width: '120px' }}>Trng Thi Bn</th>
                                            <th style={{ width: '150px' }}>Hnh ng</th>
                                        </tr>
                                    </thead>
                                    {/* Ni dung bng */}
                                    <tbody>
                                        {currentItems.map((itemData, index) => {
                                            const { type, item } = itemData;
                                            if (type === 'Currency') return null;  // n cc item loi Currency

                                            return (
                                                <tr key={index}>
                                                    {/* Cc ct thng tin */}
                                                    <td>
                                                        <span className={`badge ${type === 'Currency' ? 'badge-currency' : 'badge-asset'}`}>
                                                            {type}
                                                        </span>
                                                    </td>
                                                    <td>
                                                        {item.imageUrl ? (
                                                            <img
                                                                src={item.imageUrl}
                                                                alt={item.name}
                                                                className="item-image"
                                                                title={item.name}
                                                            />
                                                        ) : (
                                                            <div
                                                                className="bg-light d-flex align-items-center justify-content-center"
                                                                style={{ width: '40px', height: '40px', borderRadius: '4px' }}
                                                            >
                                                                <i className="fas fa-image text-muted"></i>
                                                            </div>
                                                        )}
                                                    </td>
                                                    <td>
                                                        <div className="fw-medium">{item.name || item.symbol || '-'}</div>
                                                        <small className="text-muted">ID: {truncateText(item.id, 15)}</small>
                                                    </td>
                                                    <td>
                                                        {type !== 'Currency' && (
                                                            <div className="text-truncate" style={{ maxWidth: '200px' }} title={item.description}>
                                                                {item.description || '-'}
                                                            </div>
                                                        )}
                                                    </td>
                                                    <td>
                                                        {(item.priceCents > 0 && item.status === 'Committed') ? (
                                                            <span className="badge badge-for-sale">
                                                                ang Bn
                                                                <br />
                                                                <small>{(item.priceCents / 100).toFixed(2)} USDC</small>
                                                            </span>
                                                        ) : (
                                                            <span className="badge bg-secondary">Cha Bn</span>
                                                        )}
                                                    </td>
                                                    {/* Nt hnh ng */}
                                                    <td>
                                                        {type === 'UniqueAsset' && (
                                                            <div className="d-flex gap-2">
                                                                {/* Nt chnh sa */}
                                                                <Button
                                                                    variant="outline-secondary"
                                                                    size="sm"
                                                                    onClick={() => openEditModal(item)}
                                                                >
                                                                    Sa
                                                                </Button>

                                                                {/* Nt bn/hy bn */}
                                                                {item.priceCents > 0 && item.status === 'Committed' ? (
                                                                    <Button
                                                                        variant="outline-danger"
                                                                        size="sm"
                                                                        onClick={() => handleCancelSale(item.id)}
                                                                        disabled={isProcessing}
                                                                    >
                                                                        {isProcessing ? 'ang X L...' : 'Hy Bn'}
                                                                    </Button>
                                                                ) : (
                                                                    <Button
                                                                        variant="outline-primary"
                                                                        size="sm"
                                                                        onClick={() => openListingModal(item)}
                                                                    >
                                                                        Lit K Bn
                                                                    </Button>
                                                                )}
                                                            </div>
                                                        )}
                                                    </td>
                                                </tr>
                                            );
                                        })}
                                    </tbody>

                                </table>
                            </div>
                        </div>

                        {/* Phn trang */}
                        <div className="d-flex justify-content-between align-items-center p-3">
                            <div className="d-flex align-items-center">
                                <div className="d-flex align-items-center">
                                    <span className="me-2">Hin th:</span>
                                    <select
                                        className="form-select form-select-sm"
                                        style={{ width: 'auto' }}
                                        value={itemsPerPage}
                                        onChange={(e) => {
                                            setItemsPerPage(Number(e.target.value));
                                            setCurrentPage(1); // Reset v trang u
                                        }}
                                    >
                                        {[5, 10, 20, 50].map(size => (
                                            <option key={size} value={size}>
                                                {size} mc
                                            </option>
                                        ))}
                                    </select>
                                </div>
                                <span className="ms-3 text-muted">
                                    Hin th {indexOfFirstItem + 1} - {Math.min(indexOfLastItem, items.length)}
                                    {' '}ca {items.length} mc
                                </span>
                            </div>
                            <Pagination className="mb-0">
                                <Pagination.First
                                    onClick={() => setCurrentPage(1)}
                                    disabled={currentPage === 1}
                                />
                                <Pagination.Prev
                                    onClick={() => setCurrentPage(currentPage - 1)}
                                    disabled={currentPage === 1}
                                />

                                {(() => {
                                    const paginationItems = [];
                                    const maxPagesToShow = 5;
                                    let startPage = Math.max(1, currentPage - Math.floor(maxPagesToShow / 2));
                                    let endPage = Math.min(totalPages, startPage + maxPagesToShow - 1);

                                    if (endPage - startPage + 1 < maxPagesToShow) {
                                        startPage = Math.max(1, endPage - maxPagesToShow + 1);
                                    }

                                    for (let number = startPage; number <= endPage; number++) {
                                        paginationItems.push(
                                            <Pagination.Item
                                                key={number}
                                                active={number === currentPage}
                                                onClick={() => setCurrentPage(number)}
                                            >
                                                {number}
                                            </Pagination.Item>
                                        );
                                    }

                                    return paginationItems;
                                })()}

                                <Pagination.Next
                                    onClick={() => setCurrentPage(currentPage + 1)}
                                    disabled={currentPage === totalPages}
                                />
                                <Pagination.Last
                                    onClick={() => setCurrentPage(totalPages)}
                                    disabled={currentPage === totalPages}
                                />
                            </Pagination>
                        </div>
                    </>
                )}
            </div>

            {/* Modal lit k bn */}
            <Modal show={showListingModal} onHide={() => setShowListingModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Lit K Ti Sn Bn</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {/* Hin th li nu c */}
                    {listingError && (
                        <Alert variant="danger">{listingError}</Alert>
                    )}

                    {/* Form nhp thng tin bn */}
                    <Form>
                        <Form.Group>
                            <Form.Label>Tn Ti Sn</Form.Label>
                            <Form.Control
                                type="text"
                                value={selectedItem?.name || ''}
                                readOnly
                            />
                        </Form.Group>

                        <Form.Group className="mt-3">
                            <Form.Label>Loi Tin</Form.Label>
                            <Form.Select
                                value={selectedCurrency}
                                onChange={(e) => setSelectedCurrency(e.target.value)}
                                disabled={isProcessing}
                            >
                                {availableCurrencies.map(currency => (
                                    <option key={currency.id} value={currency.id}>
                                        {currency.symbol}
                                    </option>
                                ))}
                            </Form.Select>
                        </Form.Group>

                        <Form.Group className="mt-3">
                            <Form.Label>Gi ({selectedCurrency})</Form.Label>
                            <Form.Control
                                type="number"
                                placeholder={`Nhp gi (${selectedCurrency})`}
                                value={listingPrice}
                                onChange={(e) => setListingPrice(e.target.value)}
                                min="0.01"
                                step="0.01"
                            />
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    {/* Nt hy v xc nhn */}
                    <Button
                        variant="secondary"
                        onClick={() => setShowListingModal(false)}
                        disabled={isProcessing}
                    >
                        Hy</Button>
                    <Button
                        variant="primary"
                        onClick={handleListForSale}
                        disabled={isProcessing || !listingPrice}
                    >
                        {isProcessing ? 'ang X L...' : 'Lit K Bn'}
                    </Button>
                </Modal.Footer>
            </Modal>

            {/* Modal chnh sa ti sn */}
            <Modal show={showEditModal} onHide={() => setShowEditModal(false)} size="lg">
                <Modal.Header closeButton>
                    <Modal.Title>Chnh Sa Ti Sn</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {editError && (
                        <Alert variant="danger">{editError}</Alert>
                    )}

                    <Form>
                        <Form.Group className="mb-3">
                            <Form.Label>Tn Ti Sn</Form.Label>
                            <Form.Control
                                type="text"
                                value={editItem?.name || ''}
                                readOnly
                            />
                        </Form.Group>

                        <Form.Group className="mb-3">
                            <Form.Label>Hnh nh</Form.Label>
                            <div className="d-flex gap-3 align-items-start">
                                <div className="flex-grow-1">
                                    <Form.Control
                                        type="file"
                                        accept="image/*"
                                        onChange={handleEditImageChange}
                                        disabled={isEditing}
                                    />
                                    <Form.Text className="text-muted">
                                        JPG, PNG, GIF (Max: 5MB)
                                    </Form.Text>
                                </div>
                                {(editImagePreview || editImageUrl) && (
                                    <div style={{ width: '100px', height: '100px' }}>
                                        <img
                                            src={editImagePreview || editImageUrl}
                                            alt="Preview"
                                            className="img-thumbnail"
                                            style={{ width: '100%', height: '100%', objectFit: 'cover' }}
                                        />
                                    </div>
                                )}
                            </div>
                        </Form.Group>

                        {/* Loi b trng URL hnh nh c */}

                        <div className="d-flex justify-content-between align-items-center mb-2">
                            <h6>Thuc Tnh</h6>
                            <Button
                                variant="outline-primary"
                                size="sm"
                                onClick={addAttribute}
                            >
                                Thm Thuc Tnh
                            </Button>
                        </div>

                        {editAttributes.map((attr, index) => (
                            <div key={index} className="d-flex mb-2 gap-2">
                                <Form.Control
                                    type="text"
                                    placeholder="Tn thuc tnh"
                                    value={attr.traitType}
                                    onChange={(e) => updateAttribute(index, 'traitType', e.target.value)}
                                />
                                <Form.Control
                                    type="text"
                                    placeholder="Gi tr thuc tnh"
                                    value={attr.value}
                                    onChange={(e) => updateAttribute(index, 'value', e.target.value)}
                                />
                                <Button
                                    variant="outline-danger"
                                    onClick={() => removeAttribute(index)}
                                >
                                    Xa
                                </Button>
                            </div>
                        ))}
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={() => setShowEditModal(false)}
                        disabled={isEditing}
                    >
                        Hy
                    </Button>
                    <Button
                        variant="primary"
                        onClick={handleEditAsset}
                        disabled={isEditing}
                    >
                        {isEditing ? 'ang Lu...' : 'Lu Thay i'}
                    </Button>
                </Modal.Footer>
            </Modal>

        </div>
    );
};

export default ItemsTable;