import axios from 'axios';
import 'driver.js/dist/driver.css';
import React, { useEffect, useState } from 'react';
import { driver as Driver } from 'driver.js';
import { apiKey } from '../api';

const apiBaseUrl = "https://api.gameshift.dev/nx/users";

const AuthForm = ({ setIsLoggedIn, setUserData }) => {
  // Trng thi qun l d liu biu mu
  const [formData, setFormData] = useState({
    referenceId: '',  // M nh danh duy nht ca ngi dng
    email: '',        // a ch email  xc thc
    externalWalletAddress: ''  // a ch v Solana khi ng k
  });

  // Cc trng thi qun l giao din v logic
  const [isRegistering, setIsRegistering] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [successMessage, setSuccessMessage] = useState('');
  const [isFormVisible, setIsFormVisible] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isPhantomInstalled, setIsPhantomInstalled] = useState(false);

  // Kim tra v khi to hng dn khi component c ti
  useEffect(() => {
    const checkPhantomWallet = () => {
      const { solana } = window;
      setIsPhantomInstalled(!!(solana && solana.isPhantom));
    };

    checkPhantomWallet();
    initializeDriverGuide();
  }, []);

  // Hm khi to hng dn tng bc
  const initializeDriverGuide = () => {
    const driver = new Driver({
      animate: true,
      opacity: 0.75,
      nextBtnText: 'Tip theo',
      prevBtnText: 'Quay li',
      doneBtnText: 'Hon tt',
      steps: [
        {
          element: '#referenceId-input',
          popover: {
            title: 'Reference ID',
            description: 'Nhp m tham chiu duy nht. y l m nh danh ti khon ca bn trong h thng.',
            position: 'bottom'
          }
        },
        {
          element: '#email-input',
          popover: {
            title: 'Email',
            description: 'in a ch email chnh xc. Email ny s c s dng  xc thc v khi phc ti khon.',
            position: 'bottom'
          }
        },
        {
          element: '#auth-button',
          popover: {
            title: 'Xc thc Ti khon',
            description: 'Nhn nt  hon tt qu trnh ng k hoc ng nhp. Lu  ng k cn kt ni Phantom Wallet.',
            position: 'top'
          }
        }
      ]
    });

    // T ng bt u hng dn khi trang c ti
    driver.drive();
  };

  // Kt ni vi Phantom Wallet
  const connectPhantomWallet = async () => {
    if (!isPhantomInstalled) {
      setErrorMessage('Phantom Wallet cha c ci t');
      return null;
    }

    try {
      const resp = await window.solana.connect();
      return resp.publicKey.toString();
    } catch (err) {
      setErrorMessage('Kt ni v Phantom tht bi');
      return null;
    }
  };

  // Cp nht trng thi form khi ngi dng thay i gi tr
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    setErrorMessage('');
    setSuccessMessage('');
  };

  // Xc thc d liu biu mu
  const validateForm = () => {
    if (!formData.referenceId || !formData.email) {
      setErrorMessage('Vui lng nhp y  thng tin.');
      return false;
    }
    if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) {
      setErrorMessage('Email khng hp l.');
      return false;
    }
    return true;
  };

  // X l hnh ng ng k hoc ng nhp
  const handleAction = async (isRegister) => {
    if (!validateForm()) return;

    setIsLoading(true);
    setErrorMessage('');
    setSuccessMessage('');

    try {
      if (isRegister) {
        // Quy trnh ng k chi tit
        const walletAddress = await connectPhantomWallet();
        if (!walletAddress) {
          setIsLoading(false);
          return;
        }

        // Cp nht a ch v vo form
        setFormData(prev => ({
          ...prev,
          externalWalletAddress: walletAddress
        }));

        const config = {
          headers: {
            'accept': 'application/json',
            'content-type': 'application/json',
            'x-api-key': apiKey
          }
        };

        // Gi yu cu ng k
        await axios.post(apiBaseUrl, {
          referenceId: formData.referenceId,
          email: formData.email,
          externalWalletAddress: walletAddress
        }, config);

        setSuccessMessage('ng k thnh cng!');
      } else {
        // Quy trnh ng nhp
        const config = {
          headers: {
            'accept': 'application/json',
            'x-api-key': apiKey
          }
        };

        const response = await axios.get(`${apiBaseUrl}/${formData.referenceId}`, config);

        if (response.data.email !== formData.email) {
          throw new Error('Email khng khp');
        }

        setSuccessMessage('ng nhp thnh cng!');
      }

      // Cp nht trng thi sau khi thnh cng
      setTimeout(() => {
        setUserData(formData);
        setIsLoggedIn(true);
        setIsFormVisible(false);
      }, 1500);
    } catch (err) {
      // X l cc li t pha server
      setErrorMessage(
        err.response?.status === 409
          ? 'Ti khon  tn ti.'
          : ' xy ra li. Vui lng th li sau.'
      );
    } finally {
      setIsLoading(false);
    }
  };

  // Render spinner khi form n
  if (!isFormVisible) {
    return (
      <div className="position-absolute top-50 start-50 translate-middle text-center">
        <div className="spinner-grow text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
      </div>
    );
  }

  return (
    <div className="vh-100 d-flex align-items-center justify-content-center bg-light">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-md-5 col-lg-4">
            <div className="bg-white rounded-4 p-4 shadow-sm">
              <div className="text-center mb-4">
                <h4 className="fw-bold mb-1 text-dark">
                  {isRegistering ? 'To ti khon' : 'ng nhp'}
                </h4>
                <p className="text-secondary small mb-0">
                  {isRegistering
                    ? 'Nhp thng tin  to ti khon mi'
                    : 'ng nhp  tip tc'}
                </p>
              </div>

              <form>
                <div className="mb-3">
                  <input
                    id="referenceId-input"
                    type="text"
                    className="form-control form-control-lg bg-light border-0 rounded-3"
                    placeholder="Reference ID"
                    name="referenceId"
                    value={formData.referenceId}
                    onChange={handleInputChange}
                    disabled={isLoading}
                  />
                </div>

                <div className="mb-4">
                  <input
                    id="email-input"
                    type="email"
                    className="form-control form-control-lg bg-light border-0 rounded-3"
                    placeholder="Email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    disabled={isLoading}
                  />
                </div>

                {isRegistering && !isPhantomInstalled && (
                  <div className="alert alert-warning py-2 mt-3 mb-3 text-center small">
                    Vui lng ci t Phantom Wallet  ng k
                  </div>
                )}

                <button
                  id="auth-button"
                  type="button"
                  className={`btn ${isRegistering ? 'btn-dark' : 'btn-primary'} w-100 py-3 rounded-3 position-relative overflow-hidden`}
                  onClick={() => handleAction(isRegistering)}
                  disabled={isLoading || (isRegistering && !isPhantomInstalled)}
                >
                  {isLoading ? (
                    <div className="spinner-border spinner-border-sm" role="status">
                      <span className="visually-hidden">Loading...</span>
                    </div>
                  ) : (
                    <span className="fw-semibold">
                      {isRegistering ? 'ng k' : 'ng nhp'}
                    </span>
                  )}
                </button>
              </form>

              {errorMessage && (
                <div className="alert alert-danger py-2 mt-3 mb-0 text-center small">
                  {errorMessage}
                </div>
              )}

              {successMessage && (
                <div className="alert alert-success py-2 mt-3 mb-0 text-center small">
                  {successMessage}
                </div>
              )}

              <div className="text-center mt-4">
                <button
                  type="button"
                  className="btn btn-link text-decoration-none p-0 text-secondary"
                  onClick={() => setIsRegistering(!isRegistering)}
                  disabled={isLoading}
                >
                  <small>
                    {isRegistering ? ' c ti khon? ' : 'Cha c ti khon? '}
                    <span className="text-primary fw-semibold">
                      {isRegistering ? 'ng nhp' : 'ng k'}
                    </span>
                  </small>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AuthForm;